.tot {
    position: fixed;
    width: 100%;
    height: 100vh;
}
.container {
    background-color: #ffffff;
    box-shadow: 0px 0px 10px 0px #b9b9b9;
    height: 200px;
    margin: 3em auto;
    display: table;
    padding: 2em 3em;
    border-radius: 10px;
    border: 1px solid #000;
    z-index: 100;
    position: absolute;
    right: 0em;
    left: 0em;
}
.title {
    font-family: "Playwrite CU", cursive;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    color: #CC6633;
    text-align: center;
}
.line {
    height: 1px;
    width: 100%;
    background-color: #7c7c7ccc;
}
.contQRCODE {
    text-align: center;
}
.contQR {
    display: flex;
    justify-content: center !important;
    width: 100%;
    align-items: center !important;
}
.qrcode{
    margin: 1em auto;
    height: 200px;
    display: block !important;
}
.contDate {
    margin: 1.3em auto;
    display: flex;
    justify-content: space-between;
}
.contDate h4:not(:last-child) {
    border-right: 1px solid #000;
    padding-right: 2em;
}
.contEndss {
    font-family: "Junge", cursive;
    font-weight: 500;
    font-style: normal;
    text-align: center;
    margin: 2em 0em;
}

.tangerine_regular {
    font-family: "Tangerine", cursive;
    font-weight: 600;
    font-style: normal;
    font-size: 3em;
    color: #CC6633;
  }
  .rosarivo_regular {
    font-family: "Rosarivo", cursive;
    font-weight: 400;
    font-style: normal;
  }
.contMap {
    margin-top: 3em;
}
.map {
    width: 100%;
    height: 650;
    border:none
}
.buttonLogout {
    width: 100%;
    border: none;
    padding: 1em;
    background-color: transparent;
    border-top: 1px solid #CC6633;
    margin-top: 1.5em;
}
.buttonLogout:hover {
    background-color: #ffbb99;
    border-radius: 0px 0px 10px 10px;
    transition: .2s;
}
.lora {
    font-family: "Playwrite CU", cursive;
    font-optical-sizing: auto;
    font-weight: 500;
    font-style: normal;
    margin: 1em 0em;
    text-decoration: underline;
  }

  .loraR {
    font-family: "Lora", serif;
    font-optical-sizing: auto;
    font-weight: 600;
    font-style: normal;
  }
  .att {
    font-size: 1.3em;
    text-decoration: underline;
  }