.container {
    display: flex;
    height: 100vh;
    justify-content: center;
    align-items: center;
    text-align: center;
    width: 100%;
    background-image: url('https://ae01.alicdn.com/kf/Se5848c95aa0b44d3a79c663ab508e2b1c/Rosas-falsas-para-decora-o-toque-real-terracota-bege-blush-gasto-rosa-laranja-casamento-de-outono.png');
    background-repeat: no-repeat;
    background-size: cover;
}
.img {
    height: 300px;
    display: block;
    margin: 0em auto;
}

.title {
  font-family: "Playwrite CU", cursive;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}
.button {
  padding: 1em 3em;
  border: none;
  border-radius: 10px;
  background-color: #CC6633;
  text-transform: uppercase;
  color: #fff;
  font-weight: 300;
  display: table;
  margin: 0em auto;
  text-decoration: none;
}
.background {
  width: 100%;
  z-index: 100;
  position: fixed;
  height: 100vh;
}