.container {
    background-color: rgb(255, 255, 255);
    box-shadow: 0px 0px 10px 0px #949494;
    right: 0em;
    left: 0em;
    margin: 0em auto;
    display: table;
    padding: 2em 3em;
    border-radius: 10px;
    position: absolute;

}
.tot {
  width: 100%;
  height: 100vh;
  margin-top: 2em;
  background-image: url('https://i.pinimg.com/originals/c1/a8/f9/c1a8f9909b7fd6b1bb363931150212f5.png');
}
.img_flower_top {
  top: -5em !important;
  right: -4em;
  transform: rotate(180deg);
}
.img_flower {
  height: 400px;
  position: fixed;
  bottom: 0em;
}
.line {
    height: 1px;
    width: 100%;
    background-color: #7c7c7ccc;
}
.title {
    font-family: "Playwrite CU", cursive;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    color: #CC6633;
}
.button {
    width: 100%;
    margin-top: 2em;
    font-family: "Noto Serif", serif;
    font-optical-sizing: auto;
    font-weight: 400;
    font-style: normal;
    font-variation-settings:
      "wdth" 100;
}
.button:hover {
  background-color: #fabb9c;
}


.containerForm {
    margin: 2em 0em 0em 0em;
}
.form__group {
    position: relative;
    padding: 15px 0 0;
    margin-top: 10px;
    width: 100%;
  }
  
  .form__field {
    font-family: inherit;
    width: 100%;
    border: 0;
    border-bottom: 2px solid #ccc;
    outline: 0;
    font-size: 1rem;
    color: #020202;
    padding: 7px 0;
    background: transparent;
    transition: border-color 0.2s;
  
    &::placeholder {
      color: transparent;
    }
  
    &:placeholder-shown ~ .form__label {
      font-size: 1.3rem;
      cursor: text;
      top: 20px;
    }
  }
  
  .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 1rem;
    color: #242424cc;
  }
  
  .form__field:focus {
    ~ .form__label {
      position: absolute;
      top: 0;
      display: block;
      transition: 0.2s;
      font-size: 1rem;
      color: #CC6633;
      font-weight:700;    
    }
    padding-bottom: 6px;  
    font-weight: 700;
    border-width: 3px;
    border-image: linear-gradient(to right, #CC6633,#fff);
    border-image-slice: 1;
  }
  .buttonCreate {
    width: 100%;
    margin-top: 1em;
    background-color: #913000;
    font-family: "Noto Serif", serif;
    font-optical-sizing: auto;
    font-weight: 200;
    font-style: normal;
    font-variation-settings:
      "wdth" 100;
      border: none;
  }
  .buttonCreate:hover {
    background-color: #CC6633;
  }